import React from "react";

class ModManagement extends React.Component {
    componentWillMount() {
        console.log("will mount");
    }

    render() {
        return (
            <div className="page-header">
                <h1>Moderator Management</h1>
                <p style={{ color: "gray" }}>Here you can view, appoint or remove the moderator status of accounts.</p>
            </div>
        )
    }
}

export default ModManagement;


