import React from "react";
import RequestTable from "../components/RequestTable";
import axios from "axios";
import moment from "moment";
import { DOMAIN } from "../constants";
import { FormControl } from "react-bootstrap";
import placeholder from "../res/default.png";
import { Modal, Button, Alert } from "react-bootstrap";

class MyOffer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            offer: {},
            error: "",
            loading: false,
            newOffer: {
                state: "AL",
                city: "",
                zip: "",
                budget: "",
                bedrooms:"",
                bathrooms:"",
                targetResidentCount: "",
                petsAllowed: false,
                smokingAllowed: false
            },
            modalOpen: false
        }
        this.fetchOffer = this.fetchOffer.bind(this);
        this.createOfferDisplay = this.createOfferDisplay.bind(this);
        this.offerExists = this.offerExists.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.submitOffer = this.submitOffer.bind(this);
        this.offerDisplay = this.offerDisplay.bind(this);
        this.deleteOffer = this.deleteOffer.bind(this);
        this.openModal = this.openModal.bind(this);
        this.onRequestAccept = this.onRequestAccept.bind(this);
        this.onRequestDecline = this.onRequestDecline.bind(this);
        this.onRequestDelete = this.onRequestDelete.bind(this);
        this.onInvitationDelete = this.onInvitationDelete.bind(this);
    }

    onRequestAccept(requestID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.put(DOMAIN + "/api/offer/request/" + requestID + "?status=accepted")
        .then(res => {
            this.fetchOffer();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchOffer();
        })
    }

    onRequestDecline(requestID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.put(DOMAIN + "/api/offer/request/" + requestID + "?status=denied")
        .then(res => {
            this.fetchOffer();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchOffer();
        })
    }

    onRequestDelete(requestID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.delete(DOMAIN + "/api/offer/request/" + requestID)
        .then(res => {
            this.fetchOffer();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchOffer();
        })
    }

    onInvitationDelete(requestID, offerID, userID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.delete(DOMAIN + "/api/user/" + userID + "/request")
        .then(res => {
            this.fetchOffer();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchOffer();
        })
    }

    componentWillMount() {
        this.fetchOffer();
    }

    offerExists() {
        const obj = this.state.offer;
        return !(Object.keys(obj).length === 0 && obj.constructor === Object)
    }

    handleChange(event) {
        const target = event.target;
        this.setState({
            newOffer: {
                ...this.state.newOffer,
                [target.name]: target.value
            },
            error: ""
        })
    }

    submitOffer(event) {
        event.preventDefault();
        let data = {
            ...this.state.newOffer,
            budget: parseInt(this.state.newOffer.budget, 10),
            zip: parseInt(this.state.newOffer.zip, 10),
            bedrooms: parseInt(this.state.newOffer.bedrooms, 10),
            bathrooms: parseInt(this.state.newOffer.bathrooms, 10),
            preChosenProperty: (this.state.newOffer.preChosenProperty === "true"),
            targetResidentCount: parseInt(this.state.newOffer.targetResidentCount, 10),
        }
        this.setState({ loading: true });
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.post(DOMAIN + "/api/offer", data)
            .then(res => {
                this.setState({
                    offer: res.data,
                    loading: false,
                    newOffer: {
                        state: "",
                        city: "",
                        zip: "",
                        budget: "",
                        bedrooms:"",
                        bathrooms:"",
                        targetResidentCount: "",
                        petsAllowed: false,
                        smokingAllowed: false
                    }
                })
            }).catch(err => {
                this.setState({
                    error: err.response.data.message,
                    loading: false
                });
            })
    }

    openModal(open) {
        this.setState({modalOpen: open});
    }

    deleteOffer() {
        this.setState({ loading: true });
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.delete(DOMAIN + "/api/offer")
        .then(res => {
            this.setState({
                offer: {},
                modalOpen: false,
                loading: false
            })
        }).catch(err => {
            this.setState({
                error: "Server error. Deletion failed",
                modalOpen: false,
                loading: false
            });
        })
    }

    fetchOffer() {
        this.setState({ loading: true });
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.get(DOMAIN + "/api/offer")
        .then(res => {
            this.setState({
                offer: res.data,
                loading: false
            })
        }).catch(err => {
            this.setState({
                error: "Unable to gather offer info from server",
                loading: false
            });
        })
    }

    render() {
        return (
            this.state.loading ?
                <div className="loader"></div> :
                <div>
                    {
                        !this.offerExists() ? this.createOfferDisplay() : this.offerDisplay()
                    }
                </div>
        )
    }

    offerDisplay() {
        return (
            <div>
                <div className="page-header">
                    <h1>Your Offer <span style={{color:"darkgray", cursor:"pointer"}} onClick={this.fetchOffer} className="glyphicon glyphicon-refresh"/></h1>
                    <p style={{ color: "gray" }}>Here you can view your uploaded roommate offer. You can also look at your offer's received contact requests, and any invitations you've sent out.</p>
                </div>
                {
                    this.state.error ?
                        <Alert bsStyle="danger" onDismiss={() => this.setState({error:""})}>
                            {this.state.error}
                        </Alert> : null
                }
                <div className="row">
                    <div className="col-md-3 col-md-offset-9 col-xs-12" style={{display:"flex", justifyContent:"center"}}>
                        <button onClick={() => this.openModal(true)} style={{float:"right", maxWidth:"300px"}} className="btn btn-default btn-danger btn-block">Delete Offer</button>
                    </div>
                </div>
                <Modal show={this.state.modalOpen} onHide={() => this.openModal(false)}>
                    <Modal.Header closeButton>
                        <Modal.Title>Delete your offer?</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <p>Are you sure you wish to delete your offer? This action can't be reversed.</p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={() => this.openModal(false)}>Cancel</Button>
                        <Button bsStyle="danger" onClick={this.deleteOffer}>Delete Offer</Button>
                    </Modal.Footer>
                </Modal>
                <hr/>
                <div className="row">
                    <div className="col-md-3 col-xs-12" style={{textAlign:"center"}}>
                        <img src={placeholder} alt="offer" style={{width:"100%", maxWidth:"200px"}}/>
                    </div>
                    <div className="col-md-9 col-xs-12">
                        <div className="row">
                            <div className="col-xs-4">
                                <h5><b>Upload Date</b></h5>
                                {moment(this.state.offer.CreatedAt, moment.ISO_8601).format("MMMM DD, YYYY")}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Property Type</b></h5>
                                {this.state.offer.propertyType}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Spots Filled</b></h5>
                                {this.state.offer.acceptedResidentCount + " of " + this.state.offer.targetResidentCount}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-4">
                                <h5><b>Location</b></h5>
                                {this.state.offer.city + " " + this.state.offer.state + ", " + this.state.offer.zip}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Bedrooms</b></h5>
                                {this.state.offer.bedrooms}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Bathrooms</b></h5>
                                {this.state.offer.bathrooms}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-4">
                                <h5><b>Property Already Owned</b></h5>
                                {this.state.offer.preChosenProperty ? "Yes":"No"}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Pets Allowed</b></h5>
                                {this.state.offer.petsAllowed ? "Yes":"No"}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Smoking Allowed</b></h5>
                                {this.state.offer.smokingAllowed ? "Yes":"No"}
                            </div>
                        </div>
                    </div>
                </div>
                <hr/>
                <div style={{ textAlign: "center", borderRadius:"3px", margin: "4px 0", backgroundColor: "#3055AF", color: "white" }}>
                    <h4 style={{ padding: "10px" }}>Received Contact Requests</h4>
                </div>
                <RequestTable list={this.state.offer.requests} offer communication 
                    onAccept={this.onRequestAccept}
                    onDecline={this.onRequestDecline}
                    onDelete={this.onRequestDelete}
                />
                <div style={{ textAlign: "center", borderRadius:"3px", margin: "4px 0", backgroundColor: "#3055AF", color: "white" }}>
                    <h4 style={{ padding: "10px" }}>Sent Invitations</h4>
                </div>
                <RequestTable list={this.state.offer.residents} offer sender
                    onDelete={this.onInvitationDelete}
                />
            </div>
        )
    }

    createOfferDisplay() {
        return (
            <div>
                <div className="page-header">
                    <h1>Create an Offer</h1>
                    <p style={{ color: "gray" }}>Don't feel like searching for roommate offers? Create and upload your own offer, and let others find you.</p>
                </div>
                <div className="row">
                    {
                        this.state.error ?
                            <Alert bsStyle="danger" onDismiss={() => this.setState({error:""})}>
                                {this.state.error}
                            </Alert> : null
                    }
                    <div className="col-sm-8">
                        <form onSubmit={this.submitOffer}>
                            <div className="row">
                                <div className="col-sm-12">
                                    <h4>Are you offering property you currently own?</h4>
                                    <hr />
                                    <div className="form-group ">
                                        <label className="radio-inline"><input required type="radio" name="preChosenProperty" onClick={this.handleChange} value={true} />yes</label>
                                        <label className="radio-inline"><input required type="radio" name="preChosenProperty" onClick={this.handleChange} value={false} />no</label>
                                    </div>
                                </div>
                                <div className="col-sm-12">
                                    <h4>Location:</h4>
                                    <hr />
                                </div>
                                <div className="col-sm-4">
                                    <div className="form-group">
                                        <FormControl name="state" value={this.state.newOffer.state} onChange={this.handleChange} componentClass="select" placeholder="select">
                                            <option value="AL">Alabama</option>
                                            <option value="AK">Alaska</option>
                                            <option value="AZ">Arizona</option>
                                            <option value="AR">Arkansas</option>
                                            <option value="CA">California</option>
                                            <option value="CO">Colorado</option>
                                            <option value="CT">Connecticut</option>
                                            <option value="DE">Delaware</option>
                                            <option value="DC">District Of Columbia</option>
                                            <option value="FL">Florida</option>
                                            <option value="GA">Georgia</option>
                                            <option value="HI">Hawaii</option>
                                            <option value="ID">Idaho</option>
                                            <option value="IL">Illinois</option>
                                            <option value="IN">Indiana</option>
                                            <option value="IA">Iowa</option>
                                            <option value="KS">Kansas</option>
                                            <option value="KY">Kentucky</option>
                                            <option value="LA">Louisiana</option>
                                            <option value="ME">Maine</option>
                                            <option value="MD">Maryland</option>
                                            <option value="MA">Massachusetts</option>
                                            <option value="MI">Michigan</option>
                                            <option value="MN">Minnesota</option>
                                            <option value="MS">Mississippi</option>
                                            <option value="MO">Missouri</option>
                                            <option value="MT">Montana</option>
                                            <option value="NE">Nebraska</option>
                                            <option value="NV">Nevada</option>
                                            <option value="NH">New Hampshire</option>
                                            <option value="NJ">New Jersey</option>
                                            <option value="NM">New Mexico</option>
                                            <option value="NY">New York</option>
                                            <option value="NC">North Carolina</option>
                                            <option value="ND">North Dakota</option>
                                            <option value="OH">Ohio</option>
                                            <option value="OK">Oklahoma</option>
                                            <option value="OR">Oregon</option>
                                            <option value="PA">Pennsylvania</option>
                                            <option value="RI">Rhode Island</option>
                                            <option value="SC">South Carolina</option>
                                            <option value="SD">South Dakota</option>
                                            <option value="TN">Tennessee</option>
                                            <option value="TX">Texas</option>
                                            <option value="UT">Utah</option>
                                            <option value="VT">Vermont</option>
                                            <option value="VA">Virginia</option>
                                            <option value="WA">Washington</option>
                                            <option value="WV">West Virginia</option>
                                            <option value="WI">Wisconsin</option>
                                            <option value="WY">Wyoming</option>
                                        </FormControl>
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <div className="form-group ">
                                        <input required pattern="^[a-zA-Z\s]*$" type="text" name="city" className="form-control" value={this.state.newOffer.city} onChange={this.handleChange} placeholder="City" />
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <div className="form-group">
                                        <input required type="number" name="zip" className="form-control" value={this.state.newOffer.zip} onChange={this.handleChange} placeholder="Zip" />
                                    </div>
                                </div>
                                <div className="col-sm-5">
                                    <h4>Your Monthly Budget:</h4>
                                    <hr />
                                    <div className="form-group input-group">
                                        <span className="input-group-addon">$</span>
                                        <input name="budget" required type="number" min="1.00" step="1.00" className="form-control" value={this.state.newOffer.budget} onChange={this.handleChange} placeholder="0.00" />
                                    </div>
                                </div>
                                <div className="col-sm-7">
                                    <h4>Residents (Exclude yourself):</h4>
                                    <hr />
                                    <div className="form-group">
                                        <input name="targetResidentCount" required type="number" min="1" step="1" className="form-control" value={this.state.newOffer.targetResidentCount} onChange={this.handleChange} placeholder="0" />
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <h4>Bedrooms:</h4>
                                    <hr />
                                    <div className="form-group">
                                        <input name="bedrooms" required type="number" min="1" step="1" className="form-control" value={this.state.newOffer.bedrooms} onChange={this.handleChange} placeholder="0" />
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <h4>Bathrooms:</h4>
                                    <hr />
                                    <div className="form-group">
                                        <input name="bathrooms" required type="number" min="1" step="1" className="form-control" value={this.state.newOffer.bathrooms} onChange={this.handleChange} placeholder="0" />
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <h4>Property Type:</h4>
                                    <hr />
                                    <div className="form-group ">
                                        <label className="radio-inline"><input required type="radio" name="propertyType" onClick={this.handleChange} value="house" />house</label>
                                        <label className="radio-inline"><input required type="radio" name="propertyType" onClick={this.handleChange} value="apartment" />apartment</label>
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <h4>Gender Requirement:</h4>
                                    <hr />
                                    <div className="form-group ">
                                        <label className="radio-inline"><input required type="radio" name="genderRequirement" onClick={this.handleChange} value="none" />Doesn't matter</label>
                                        <label className="radio-inline"><input required type="radio" name="genderRequirement" onClick={this.handleChange} value={this.props.user.gender} />Same as me</label>
                                    </div>
                                </div>
                                <div className="col-sm-12">
                                    <h4>Additional:</h4>
                                    <hr />
                                    <div className="checkbox">
                                        <label><input type="checkbox" checked={this.state.newOffer.smokingAllowed} onChange={event => {
                                            this.setState({ newOffer: { ...this.state.newOffer, smokingAllowed: event.target.checked } });
                                        }} />Smoking Allowed</label>
                                    </div>
                                    <div className="checkbox">
                                        <label><input type="checkbox" checked={this.state.newOffer.petsAllowed} onChange={event => {
                                            this.setState({ newOffer: { ...this.state.newOffer, petsAllowed: event.target.checked } });
                                        }} />Pets Allowed</label>
                                    </div>
                                </div>
                            </div>
                            <button type="submit" style={{ marginTop: "10px" }} className="btn btn-default btn-primary">Upload Offer</button>
                        </form>
                    </div>
                    <div className="col-sm-4">
                        <h4>Property Image:</h4>
                        <hr />
                        <div className="row">
                            <div className="col-sm-12 col-sm-offset-0 col-xs-6 col-xs-offset-3">
                                <img src={placeholder} alt="offer" style={{width:"100%"}}/>
                            </div>
                            <div className="col-sm-12 col-sm-offset-0 col-xs-6 col-xs-offset-3">
                                <button className="btn btn-default btn-block" style={{marginTop:"10px"}}>Upload Image</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default MyOffer;


