import React from "react";
import axios from "axios";
import { DOMAIN } from "../constants";

class UserManagement extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            flags: [],
            reports: [],
            banned: [],
            loading: false,
            error: ""
        }
        this.fetchLists = this.fetchLists.bind(this);
    }

    componentWillMount() {
        //this.fetchLists();
    }

    fetchLists() {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.get(DOMAIN + "/api/user/report")
        .then(res => {
            this.setState({
                reports: res.data
            })
            instance.get(DOMAIN + "/api/offer/flag")
            .then(res => {
                this.setState({
                    flags: res.data
                })
                instance.get(DOMAIN + "/api/user/ban")
                .then(res => {
                    this.setState({
                        banned: res.data,
                        loading: false
                    })
                })
                .catch(err => {
                    this.setState({
                        error: "Unable to gather banned users from server",
                        loading: false
                    });
                })
            })
            .catch(err => {
                this.setState({
                    error: "Unable to gather flagged offers from server",
                    loading: false
                });
            })
        }).catch(err => {
            this.setState({
                error: "Unable to gather reported users from server",
                loading: false
            });
        })
    }

    render() {
        return (
            this.state.loading ?
            <div className="loader"></div>:
            <div className="page-header">
                <h1>User Management</h1>
                <p style={{ color: "gray" }}>Here you can take action on reports, flags and banned users.</p>
            </div>
        )
    }
}

export default UserManagement;


