import React from "react";

const Tag = props => {
    let color;
    switch(props.status) {
        case "accepted": color = "#BBE5B3"
            break;
        case "denied": color = "#FEAD9A"
            break;
        case "pending": color = "#FFEA8A"
            break;
        default:
            color = "gray";
    }
    return (
        <span style={{backgroundColor:color, borderRadius:"14px", padding:"2px 10px"}}>
            <small>{props.status}</small>
        </span>
    )
}

export default Tag;


