import React, { Component } from 'react';
import axios from "axios";
import moment from "moment";
import Home from "./components/Home";
import MyOffer from "./containers/MyOffer";
import MyRequests from "./containers/MyRequests";
import UserManagement from "./containers/UserManagement";
import ModManagement from "./containers/ModManagement";
import NotFound from "./components/NotFound";
import NavBar from "./components/Navbar";
import Profile from "./containers/Profile";
import OtherProfile from "./containers/OtherProfile";
import PrivateRoute from "./components/PrivateRoute";
import { Switch, Route, Redirect } from "react-router-dom";
import { DOMAIN } from "./constants";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loggedIn: false,
            loginPending: false,
            error: "",
            registrationSuccess: false,
            loading: false
        }
        this.confirmEmail = this.confirmEmail.bind(this);
        this.login = this.login.bind(this);
        this.logout = this.logout.bind(this);
        this.updateUser = this.updateUser.bind(this);
    }

    componentWillMount() {
        const token = localStorage.getItem("token");
        if (token) {
            this.setState({loading: true});
            const instance = axios.create({
                headers: { 'Authorization': 'Bearer ' + token }
            })
            instance.get(DOMAIN + "/api/user")
            .then(res => {
                res.data.birthdate = moment(res.data.birthdate, "YYYY-MM-DD").format("YYYY-MM-DD");
                this.setState({
                    user: res.data,
                    token: token,
                    loggedIn: true,
                    loading: false
                });
            }).catch(err => {
                localStorage.removeItem("token");
                this.setState({loading:false});
            })
        }
    }

    updateUser(user) {
        this.setState({user});
    }

    login(info) {
        this.setState({loginPending: true});
        axios.post(DOMAIN + "/api/auth/login", info)
        .then(res => {
            res.data.user.birthdate = moment(res.data.user.birthdate, "YYYY-MM-DD").format("YYYY-MM-DD");
            localStorage.setItem("token", res.data.token);
            this.setState({
                user: res.data.user,
                token: res.data.token,
                loggedIn: true,
                loginPending: false
            })
        }).catch(err => {
            let errorMsg = "Couldn't get response from server.";
            if (err.response) {
                errorMsg = err.response.data.message
            }
            this.setState({
                error: errorMsg,
                loginPending: false
            })
        })
    }

    logout() {
        this.setState({
            token: undefined,
            loggedIn: false,
            user: {}
        })
        localStorage.removeItem("token");
    }

    confirmEmail(append) {
        this.props.history.push({pathname: this.props.location.pathname, search:""})
        axios.get(DOMAIN + "/api/auth/signup/confirmation" + append)
        .then(res => {
            this.setState({
                registrationSuccess: true
            })
        })
        .catch(err => {
            this.setState({
                error: "Failed to confirm registration."
            })
        })
    }

    render() {
        return (
            this.state.loading ?
            <div style={{textAlign:"center"}}>
                <div className="loader"></div>
                <h1>Roommates 40+</h1>
            </div>:
            <Switch>
                <NavBar user={this.state.user} login={this.login} logout={this.logout} loggedIn={this.state.loggedIn} pending={this.state.loginPending}>
                    <Switch>
                        <Route exact path="/" render={() => this.state.loggedIn ? <Redirect to="/profile/requests"/>:<Home confirmEmail={this.confirmEmail} 
                            error={this.state.error} success={this.state.registrationSuccess}/>}/>
                        <PrivateRoute path="/profile" loggedIn={this.state.loggedIn} render={() => (
                            <Profile user={this.state.user} logout={this.logout} token={this.state.token} updateUser={this.updateUser}>
                                <Switch>
                                    <Route path="/profile/offer" render={() => <MyOffer token={this.state.token} user={this.state.user}/>} />
                                    <Route path="/profile/requests" render={() => <MyRequests token={this.state.token}/>} />
                                    <Route path="/profile/management/user" render={() => <UserManagement token={this.state.token}/>} />
                                    <Route path="/profile/management/mod" render={() => <ModManagement token={this.state.token}/>} />
                                    <Route component={NotFound}/>
                                </Switch>
                            </Profile>
                        )} />
                        <PrivateRoute path="/user/:id" loggedIn={this.state.loggedIn} render={() => (
                            <OtherProfile token={this.state.token} />
                        )}/>
                        <PrivateRoute loggedIn={this.state.loggedIn} component={NotFound}/>
                    </Switch>
                </NavBar>
            </Switch>
        );
    }
}

export default App;



