import React from "react";
import Tag from "./Tag";
import moment from "moment";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom"; 

const RequestTable = props => {
    let requests = props.list.map((request, index) => (
        <tr key={request.ID}>
            <td style={{width:"120px"}}>{request.user.firstname + " " + request.user.lastname}</td>
            <td style={{textAlign:"left"}}>
                {
                    props.offer ? <Link to={"/user/" + request.userID}>View Profile</Link>:
                    <Link to={"/user/" + request.offer.uploaderID}>View Profile</Link>
                }
            </td>
            <td>{moment(request.CreatedAt, moment.ISO_8601).format("MMMM DD, YYYY")}</td>
            {
                props.communication? <td>{request.user.email ? request.user.email:"Hidden"}</td>:null
            }
            <td style={{width:"80px", textAlign:"right"}}><Tag status={request.status}/></td>
            <td style={{width:"140px", textAlign:"right"}}>
                {
                    props.sender || request.status === "accepted" ?
                    <Button bsSize="xsmall" bsStyle="danger" onClick={() => props.onDelete(request.ID, request.offerID, request.userID)}>Delete</Button>:
                    <div>
                        <Button bsSize="xsmall" bsStyle="primary" style={{marginRight:"2px"}} onClick={() => props.onAccept(request.ID)}>Accept</Button>
                        <Button bsSize="xsmall" bsStyle="danger" onClick={() => props.onDecline(request.ID)}>Decline</Button>
                    </div>
                }
            </td>
        </tr>
    ))
    return (
        <div className="table-responsive" style={{marginTop:"4px"}}>
            {
                props.list.length === 0 ?
                <div style={{color:"gray", textAlign:"center"}}><h4>None Yet...</h4></div>:null
            }
            <table className="table">
                {
                    props.list.length > 0 ?
                    <thead style={{color:"#555555"}}>
                        <tr>
                            <th>{props.sender ? "Sent to":"Received from"}</th>
                            <th></th>
                            <th>{props.sender ? "Sent on":"Received on"}</th>
                            {
                                props.communication ? <th>Email</th>:null
                            }
                            <th style={{textAlign:"right"}}>Status</th>
                            <th style={{textAlign:"right"}}>Actions</th>
                        </tr>
                    </thead>:null
                }
                <tbody>
                    {requests}
                </tbody>
            </table>
        </div>
    )
}

export default RequestTable;


