import React from "react";
import moment from "moment";
import axios from "axios";
import { Modal } from "react-bootstrap";
import { DOMAIN } from "../constants";
import placeholder from "../res/default.png";

class User extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userEdit: {
                ...this.props.user
            },
            pendingEdit: false,
            error: "",
            modalOpen: false
        }
        this.updateUserEdit = this.updateUserEdit.bind(this);
        this.resetUserEdit = this.resetUserEdit.bind(this);
        this.submitEdit = this.submitEdit.bind(this);
        this.openModal = this.openModal.bind(this);
    }

    updateUserEdit(event) {
        const target = event.target;
        this.setState({
            userEdit: {
                ...this.state.userEdit,
                [target.name]: target.value
            },
            error: ""
        })
    }

    resetUserEdit() {
        this.setState({
            userEdit: {
                ...this.props.user
            },
            error: ""
        })
    }

    openModal(open) {
       this.setState({
           modalOpen: open
       }) 
    }

    submitEdit() {
        this.setState({pendingEdit: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.put(DOMAIN + "/api/user", this.state.userEdit)
        .then(res => {
            this.setState({
                pendingEdit: false,
                modalOpen: false
            })
            this.props.updateUser(res.data);
        }).catch(err => {
            this.setState({
                error: err.response.data.message,
                pendingEdit: false
            })
        })
    }

    render() {
        return (
            <div>
                <img src={placeholder} alt="profile" style={{ width: "100%", maxWidth: "240px" }} />
                <div style={{ color: "#5B5B5B" }}>
                    <h4>{this.props.user.firstname + " " + this.props.user.lastname}</h4>
                </div>
                <hr />
                <div className="row" style={{ textAlign: "left", color: "#5B5B5B" }}>
                    <div className="col-md-12 col-sm-4 col-xs-12">
                        <h4><b>Info</b></h4>
                        <div>
                            {
                                this.props.editable ? <div><b>Email:</b> {this.props.user.email}</div>:null
                            }
                        </div>
                        <div>
                            <b>First Name:</b> {this.props.user.firstname}
                        </div>
                        <div>
                            <b>Last Name:</b> {this.props.user.lastname}
                        </div>
                        <div>
                            <b>Birthday:</b> {moment(this.props.user.birthdate, "YYYY-MM-DD").format("MMMM DD, YYYY")}
                        </div>
                        <div>
                            <b>Gender:</b> {this.props.user.gender}
                        </div>
                        {
                            this.props.editable ?
                            <div style={{ display: "flex", justifyContent: "center" }}>
                                <button style={{ margin: "10px 0", maxWidth: "300px" }} type="button" className="btn btn-default btn-block"
                                    onClick={() => {
                                        this.resetUserEdit();
                                        this.openModal(true);
                                    }}>
                                    Edit Profile
                                </button>
                            </div>:null
                        }
                    </div>
                    <div className="col-md-12 col-sm-8 col-xs-12">
                        {
                            this.props.user.about ? 
                            <div>
                                <h4><b>About Me</b></h4>
                                {this.props.user.about}
                            </div> :
                            null
                        }
                    </div>
                    <div className="col-xs-12">
                        {
                            this.props.user.tags.length > 0 ? 
                            <div>
                                <h4><b>Tags</b></h4>
                            </div> :
                            null
                        }
                    </div>
                </div>
                <hr/>
                <Modal show={this.state.modalOpen} onHide={() => this.openModal(false)}>
                    <Modal.Header closeButton>
                        <Modal.Title>Edit your profile</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <form onSubmit={e => {
                                e.preventDefault();
                                this.submitEdit();
                            }}>
                            {
                                this.state.error ?
                                    <div className="alert alert-danger">
                                        {this.state.error}
                                    </div> : null
                            }
                            <div className="form-group">
                                <label>First Name:</label>
                                <input required pattern="[a-zA-Z]+" type="text" name="firstname" className="form-control" placeholder="First name" value={this.state.userEdit.firstname} onChange={e => this.updateUserEdit(e)} />
                            </div>
                            <div className="form-group">
                                <label>Last Name:</label>
                                <input required pattern="[a-zA-Z]+" type="text" name="lastname" className="form-control" placeholder="Last name" value={this.state.userEdit.lastname} onChange={e => this.updateUserEdit(e)} />
                            </div>
                            <div className="form-group">
                                <label>Birthday:</label>
                                <input required type="date" name="birthdate" className="form-control" value={moment(this.state.userEdit.birthdate, "YYYY-MM-DD").format("YYYY-MM-DD")} onChange={e => this.updateUserEdit(e)} />
                            </div>
                            <div className="form-group ">
                                <label className="radio-inline"><input required type="radio" name="gender" value="male" checked={this.state.userEdit.gender === "male"} onChange={e => this.updateUserEdit(e)} />Male</label>
                                <label className="radio-inline"><input required type="radio" name="gender" value="female" checked={this.state.userEdit.gender === "female"} onChange={e => this.updateUserEdit(e)} />Female</label>
                            </div>
                            <div className="form-group">
                                <label>About Me:</label>
                                <textarea className="form-control" rows="5" name="about" value={this.state.userEdit.about} onChange={e => this.updateUserEdit(e)}></textarea>
                            </div>
                            <hr />
                            <button type="submit" disabled={this.state.pendingEdit} className="btn btn-default">{this.state.pendingEdit ? <i className="fa fa-spinner fa-spin"></i> : null} Update</button>
                            <button style={{ float: "right" }} type="button" className="btn btn-default" onClick={() => this.openModal(false)}>Cancel</button>
                        </form>
                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}

export default User;


