import React from "react";
import { Link } from "react-router-dom";

const NavBar = props => {
    return (
        <div>
            <nav style={{ backgroundColor: "#3055AF", borderColor: "#3D5B97", color:"white" }} className="navbar navbar-default navbar-fixed-top">
                <div className="container">
                    <div className="navbar-header">
                        <button style={{ backgroundColor: "#3055AF", borderColor: "#3D5B97" }} type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                            <span style={{ backgroundColor: "white" }} className="icon-bar"></span>
                            <span style={{ backgroundColor: "white" }} className="icon-bar"></span>
                            <span style={{ backgroundColor: "white" }} className="icon-bar"></span>
                        </button>
                        <a style={{ color: "white" }} className="navbar-brand">Roommates 40+</a>
                    </div>
                    <div style={{ borderColor: "#3D5B97" }} className="collapse navbar-collapse" id="myNavbar">
                        {
                            props.loggedIn ? null :
                                <form className="navbar-form navbar-right" style={{ borderColor: "#3D5B97" }} onSubmit={e => {
                                    e.preventDefault()
                                    let email = document.getElementById("login-email").value;
                                    let password = document.getElementById("login-password").value
                                    props.login({ email, password })
                                }}>
                                    <div className="form-group">
                                        <input id="login-email" required style={{ margin: "1px" }} type="email" className="form-control input-sm" placeholder="Email" />
                                        <input id="login-password" required style={{ margin: "1px" }} type="password" minLength="8" autoComplete="new-password" className="form-control input-sm" placeholder="Password" />
                                    </div>
                                    <button type="submit" disabled={props.pending} className="btn btn-default btn-sm">{props.pending ? <i className="fa fa-spinner fa-spin"></i>:null} Login</button>
                                </form>
                        }
                        {
                            props.loggedIn ?
                                <ul className="nav navbar-nav navbar-right">
                                    <li><Link to="/blog">Blog</Link></li>
                                    <li className="dropdown">
                                        <a className="dropdown-toggle" style={{cursor:"pointer"}} data-toggle="dropdown">
                                            Notifications
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li><a style={{cursor:"pointer"}}>Coming Soon</a></li>
                                        </ul>
                                    </li>
                                    <li className="dropdown">
                                        <a className="dropdown-toggle" style={{cursor:"pointer"}} data-toggle="dropdown">
                                            {props.user.firstname + " " + props.user.lastname} <span className="caret"></span>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li><Link to="/profile/requests">Profile</Link></li>
                                            <li><a style={{cursor:"pointer"}} onClick={props.logout}>Logout</a></li>
                                        </ul>
                                    </li>
                                </ul> : null
                        }
                    </div>
                </div>
            </nav>
            {props.children}
        </div>
    )
}

export default NavBar;



