import React from "react";
import RequestTable from "../components/RequestTable";
import axios from "axios";
import { DOMAIN } from "../constants";
import { Alert } from "react-bootstrap";

class MyRequests extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user: {},
            loading: false,
            error: ""
        }
        this.fetchUser = this.fetchUser.bind(this);
        this.onRequestAccept = this.onRequestAccept.bind(this);
        this.onRequestDecline = this.onRequestDecline.bind(this);
        this.onCommRequestDelete = this.onCommRequestDelete.bind(this);
        this.onInvitationDelete = this.onInvitationDelete.bind(this);
    }

    componentWillMount() {
        this.fetchUser();
    }

    fetchUser() {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.get(DOMAIN + "/api/user")
        .then(res => {
            this.setState({
                user: res.data,
                loading: false
            })
        }).catch(err => {
            this.setState({
                error: "Unable to gather request info from server",
                loading: false
            });
        })
    }

    onRequestAccept(requestID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.put(DOMAIN + "/api/user/request/" + requestID + "?status=accepted")
        .then(res => {
            this.fetchUser();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchUser();
        })
    }

    onRequestDecline(requestID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.put(DOMAIN + "/api/user/request/" + requestID + "?status=denied")
        .then(res => {
            this.fetchUser();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchUser();
        })
    }

    onCommRequestDelete(requestID, offerID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.delete(DOMAIN + "/api/offer/" + offerID + "/request")
        .then(res => {
            this.fetchUser();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchUser();
        })
    }

    onInvitationDelete(requestID) {
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.delete(DOMAIN + "/api/user/request/" + requestID)
        .then(res => {
            this.fetchUser();
        }).catch(err => {
            this.setState({
                error: err.response.data.message
            });
            this.fetchUser();
        })
    }

    render() {
        return (
            this.state.loading ?
            <div className="loader"></div>:
            <div>
                <div className="page-header">
                    <h1>Your Requests <span style={{color:"darkgray", cursor:"pointer"}} onClick={this.fetchUser} className="glyphicon glyphicon-refresh"/></h1>
                    <p style={{ color: "gray" }}>Here you can view your sent communication requests, and received invitations to other offers.</p>
                </div>
                {
                    this.state.error ?
                        <Alert bsStyle="danger" onDismiss={() => this.setState({error:""})}>
                            {this.state.error}
                        </Alert> : null
                }
                <div style={{ textAlign: "center", borderRadius:"3px", margin: "4px 0", backgroundColor: "#3055AF", color: "white" }}>
                    <h4 style={{ padding: "10px" }}>Sent Contact Requests</h4>
                </div>
                <RequestTable list={this.state.user.requests} sender communication
                    onDelete={this.onCommRequestDelete}
                />
                <div style={{ textAlign: "center", borderRadius:"3px", margin: "4px 0", backgroundColor: "#3055AF", color: "white" }}>
                    <h4 style={{ padding: "10px" }}>Received Invitations</h4>
                </div>
                <RequestTable list={this.state.user.invitations}
                    onAccept={this.onRequestAccept}
                    onDecline={this.onRequestDecline}  
                    onDelete={this.onInvitationDelete}  
                />
            </div>
        )
    }
}

export default MyRequests;


