import React from "react";
import { Route, Redirect } from "react-router-dom";

const PrivateRoute = ({ render, loggedIn, ...rest }) => (
    <Route
        {...rest}
        render={(props) => loggedIn
            ? render()
            : <Redirect to="/" />}
    />
);

export default PrivateRoute;


