import React from "react";
import User from "../components/User";
import axios from "axios";
import moment from "moment";
import { DOMAIN } from "../constants";
import { withRouter } from "react-router-dom";
import { Tab, Tabs } from "react-bootstrap";

class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user: this.props.user
        }
        this.fetchUser = this.fetchUser.bind(this);
        this.updateUser = this.updateUser.bind(this);
        this.handleTabSelect = this.handleTabSelect.bind(this);
    }

    componentWillMount() {
        this.fetchUser();
    }

    handleTabSelect(key) {
        this.props.history.push(key);
    }

    fetchUser() {
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.get(DOMAIN + "/api/user")
        .then(res => {
            res.data.birthdate = moment(res.data.birthdate, "YYYY-MM-DD").format("YYYY-MM-DD");
            this.setState({
                user: res.data
            })
        }).catch(err => {
            this.props.logout();
        })
    }

    updateUser(user) {
        this.setState({user});
        this.props.updateUser(user);
    }   

    render() {
        return (
            <div className="container" style={{ marginTop: "52px" }}>
                <div className="row" style={{ padding: "10px 0" }}>
                    <div className="col-md-3" style={{textAlign: "center" }}>
                        <hr/>
                        <User user={this.state.user} token={this.props.token} updateUser={this.updateUser} editable />
                    </div>
                    <div className="col-md-9">
                        <div style={{padding:"18px 0"}}>
                            <Tabs
                                activeKey={this.props.location.pathname}
                                onSelect={this.handleTabSelect}
                                id="profile-tabs"
                            >
                                <Tab eventKey={"/profile/requests"} title="Requests"/>
                                <Tab eventKey={"/profile/offer"} title="Offer"/>
                                {
                                    this.state.user.adminLevel === "moderator" || this.state.user.adminLevel === "susan" ?
                                    <Tab eventKey={"/profile/management/user"} title="User Management"/>:null
                                }
                                {
                                    this.state.user.adminLevel === "susan" ?
                                    <Tab eventKey={"/profile/management/mod"} title="Moderator Management"/>:null
                                }
                            </Tabs>
                            <div style={{paddingTop:"10px"}}>
                                {this.props.children}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Profile);


