import React from "react";
import { withRouter } from "react-router-dom";
import { DOMAIN } from "../constants";
import axios from "axios";
import User from "../components/User";
import moment from "moment";
import placeholder from "../res/default.png";
import { Alert } from "react-bootstrap";

class OtherProfile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user: {},
            me: {},
            myOffer: {},
            offer: {},
            loading: false,
            error: "",
            notFound: false,
            success:""
        }
        this.fetchInfo = this.fetchInfo.bind(this);
        this.offerExists = this.offerExists.bind(this);
        this.sendContactRequest = this.sendContactRequest.bind(this);
        this.sendInvitation = this.sendInvitation.bind(this);
    }

    componentWillMount() {
        this.fetchInfo();
    }

    offerExists() {
        const obj = this.state.offer;
        return !(Object.keys(obj).length === 0 && obj.constructor === Object) && this.state.offer.ID
    }

    sendContactRequest() {
        this.setState({loading: true, success:"", error:""});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.post(DOMAIN + "/api/offer/" + this.state.offer.ID + "/request")
        .then(res => {
            this.setState({
                loading: false,
                success:"Contact request sent"
            })
        }).catch(err => {
            this.setState({
                error: err.response.data.message,
                loading: false
            })
            this.fetchInfo();
        })
    }

    sendInvitation() {
        this.setState({loading: true, success:"", error:""});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.post(DOMAIN + "/api/user/" + this.state.user.ID + "/request")
        .then(res => {
            this.setState({
                loading: false,
                success:"Invitation sent"
            })
        }).catch(err => {
            this.setState({
                error: err.response.data.message,
                loading: false
            })
            this.fetchInfo();
        })
    }

    fetchInfo() {
        const userID = this.props.match.params.id;
        this.setState({loading: true});
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.get(DOMAIN + "/api/user/" + userID)
        .then(res => {
            this.setState({
                user: res.data.user,
                offer: res.data.offer,
                loading: false
            })
        }).catch(err => {
            this.setState({
                error: err.response.data.message,
                loading: false,
                notFound: true
            })
        })
    }

    renderOffer() {
        return (
            <div>
                <div className="row">
                    <div className="col-md-3 col-xs-12" style={{textAlign:"center"}}>
                        <img src={placeholder} alt="offer" style={{width:"100%", maxWidth:"200px"}}/>
                    </div>
                    <div className="col-md-9 col-xs-12">
                        <div className="row">
                            <div className="col-xs-4">
                                <h5><b>Upload Date</b></h5>
                                {moment(this.state.offer.CreatedAt, moment.ISO_8601).format("MMMM DD, YYYY")}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Property Type</b></h5>
                                {this.state.offer.propertyType}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Spots Filled</b></h5>
                                {this.state.offer.acceptedResidentCount + " of " + this.state.offer.targetResidentCount}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-4">
                                <h5><b>Location</b></h5>
                                {this.state.offer.city + " " + this.state.offer.state + ", " + this.state.offer.zip}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Bedrooms</b></h5>
                                {this.state.offer.bedrooms}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Bathrooms</b></h5>
                                {this.state.offer.bathrooms}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-4">
                                <h5><b>Property Already Owned</b></h5>
                                {this.state.offer.preChosenProperty ? "Yes":"No"}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Pets Allowed</b></h5>
                                {this.state.offer.petsAllowed ? "Yes":"No"}
                            </div>
                            <div className="col-xs-4">
                                <h5><b>Smoking Allowed</b></h5>
                                {this.state.offer.smokingAllowed ? "Yes":"No"}
                            </div>
                        </div>
                    </div>
                </div>
                <hr/>
                <button className="btn btn-default btn-primary" style={{float:"right"}} onClick={this.sendContactRequest}>Send Contact Request</button>
            </div>
        )
    }

    render() {
        return (
            this.state.loading ?
            <div className="loader"></div>:
            this.state.notFound ?
            <div style={{textAlign:"center", color:"gray", marginTop:"62px"}}>
                <h1>User Not Found</h1>
            </div>:
            <div className="container" style={{ marginTop: "52px" }}>
                <div className="row" style={{ padding: "10px 0" }}>
                    <div className="col-md-3" style={{textAlign: "center" }}>
                        <hr/>
                        <div style={{display:"flex", justifyContent:"center"}}>
                            <button className="btn btn-default btn-danger btn-block" style={{marginBottom:"10px", maxWidth:"240px"}} onClick={this.sendInvitation}>
                                Invite to your offer
                            </button>
                        </div>
                        <User user={this.state.user} />
                    </div>
                    <div className="col-md-9">
                    <hr/>
                        {
                            this.state.error ?
                                <Alert bsStyle="danger" onDismiss={() => this.setState({error:""})}>
                                    {this.state.error}
                                </Alert> : null
                        }
                        {
                            this.state.success ?
                                <Alert bsStyle="success" onDismiss={() => this.setState({success:""})}>
                                    {this.state.success}
                                </Alert> : null
                        }
                        <div style={{ textAlign: "center", borderRadius:"3px", margin: "18px 0 16px 0", backgroundColor: "#3055AF", color: "white" }}>
                            <h4 style={{ padding: "10px" }}>{this.state.user.firstname}'s Offer</h4>
                        </div>
                        {
                            this.offerExists() ?
                            this.renderOffer():<div style={{textAlign:"center", color:"gray"}}><h2>No Offer Found</h2><hr/></div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(OtherProfile);


